e), because their privacy settings by default often don't respect your privacy. It's suggested to choose a browser that has good security settings from the start, and also isn't bloated by hidden features that sometimes can't be turned off.")])])]),_c('div',{staticClass:"danger"},[_c('div',{staticClass:"danger-2-ico"},[_c('i',{staticClass:"icons fas fa-share-alt-square"})]),_c('div',{staticClass:"danger-text"},[_c('p',{staticClass:"tip-title"},[_vm._v("Social media")]),_c('p',{staticClass:"tip-text"},[_vm._v("For messaging, avoid SMS services altogether. They can be intercepted by mobile service providers and hackers alike. Instead use a service that supports end to end encryption. Signal is a good open end to end encrypted messenger! It's still a centralised service though, thus susceptible to backdoor access and can be acquired by someone who doesn't value the end user's privacy.")])])])])]),_c('div',{staticClass:"oh-no-section"},[_c('div',{staticClass:"tip-title"},[_vm._v("\n        Oh no! What do I do?\n      ")]),_c('div',{staticClass:"tip-text"},[_vm._v(" Glad you asked.")]),_c('p',{staticClass:"tip-text"},[_vm._v("\nStart protecting your data by changing habits and using a correct technology! It comes with a drawback though. Being secure is not easy. It's simple to just click on things in Chrome. Drawback? Google knows everything about you. And it can sell this knowledge to other interested parties. What is convenient is very insecure, security comes with inconvenience. We are here to tell you, that a little bit of inconvenience can go a long way. Web is not being designed to respect your privacy. Quite the contrary, actually. Lets work on changing your browser habits right away!\n      ")])]),_c('h2',{staticClass:"tips-title",attrs:{"id":"habits"}},[_vm._v("Change your habits")]),_c('div',{staticClass:"browser-tips-section"},[_c('div',{staticClass:"tip-1 tip"},[_c('img',{staticClass:"settings-img separate-img",attrs:{"src":require("../assets/images/mail.jpg"